body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;

  background-color: #f9f9f9;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  text-decoration: none;
  outline: none;
}

h1,
h2,
h3 {
  text-transform: uppercase;
  margin-top: 0;
}

.header {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: #fff;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  border-bottom: 1px solid #eee;
}

.header > * {
  display: inline-block;
  color: #333;
  text-decoration: none;
  margin: 0 10px;
}

.container {
  max-width: 580px;
  width: 100%;
  margin: 70px auto 0 auto;
}

.timer {
  font-size: 24px;
}

.launch-agencies img {
  margin: -3px 4px;
}

.launch-agencies a {
  float: left;
  background-color: #44e;
  color: #fff;
  padding: 4px 7px;
  border-radius: 3px;
  font-size: 13px;
  text-decoration: none;
  margin: 0 5px 5px 0;
}

.launch-desc {
  line-height: 1.5;
  font-size: 14px;
  padding: 10px;
}

.launch-type {
  background-color: #292;
  color: #fff;
  padding: 4px 7px;
  border-radius: 3px;
  font-size: 13px;
  margin-bottom: 10px;
  float: left;
}

.launch-tags {
  width: 100%;
  overflow: auto;
}

.launch-rocket-name a {
  color: #fff;
  text-decoration: none;
}

.launch-link {
  background-color: #929;
  text-decoration: none;
  color: #fff;
  padding: 4px 7px;
  border-radius: 3px;
  font-size: 13px;
  margin: 0 5px 5px 0;
  float: left;
}

#footer {
  width: 100%;
  line-height: 60px;
  font-size: 12px;
  text-align: center;
  color: #888;
}

#footer a {
  color: #000;
}



